#typearea {
  width: 100vw;
  height: 100vh;
  background-color: white;
  /* border: 5px outset red; */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

#typearea-content {
  margin-top: 5vh;
  margin-bottom: 5vh;
  /* margin-left: 5vh; */
  /* margin-right: 0; */
  height: 90vh;
  width: 88%;
  /* background-color: aqua; */
  padding-left: 20px;
}
#reference-letters {
  width: 100%;
  height: 49%;
  margin-bottom: 1%;
  /* background-color: black; */
}
.wrong-letter {
  color: #ac3939;
}
.right-letter {
  color: #0c880a;
}

#type-letters {
  width: 100%;
  height: 49%;

  /* background-color: rebeccapurple; */
}
#type-letters-textarea {
  border: 0;
  height: 100%;
  width: 100%;
  resize: none;
  overflow: auto;
  border-radius: 15px;
  background-color: #ededed;
  outline: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 5px;
}
#typearea-option {
  margin-top: 5vh;
  margin-bottom: 5vh;
  /* margin-right: 5vh;
    margin-left: 0; */
  height: 90vh;
  width: 6%;

  text-align: center;
  /* background-color: blue; */

  padding-right: 20px;
}
.select-time-button {
  margin: 5px;
  padding: 0;
  border: 0;
  height: 20px;
  width: 90px;
  border-radius: 15px;
  /* background-color: #F6F6F6; */
}

.exit-button,
.ready-button,
.pause-button,
.stop-button {
  margin: 5px;
  padding: 0;
  border: 0;

  font-size: 140%;
  height: 40px;
  width: 90px;

  border-radius: 15px;
  color: white;
}
.ready-button {
  background-color: #0c880a;
}
.exit-button {
  background-color: #d41212;
}
.pause-button {
  background-color: rgb(201, 166, 43);
}
.stop-button {
  background-color: #d41212;
}
